.search-box{
    position:absolute;
    top:4rem;
    width: 10%;
    right:0;
    padding:0 0.5rem;
    box-sizing:border-box;
    max-width:400px;
    animation:expand 300ms ease-in-out forwards;
}
@keyframes expand{
    to{
        width:100%;
    }
}
@media screen and (min-width: 600px) {
    .search-box{
        top:1.35rem;
        right:2.5rem;
    }
}
@media screen and (min-width: 1024px) {
.search-box{
        top:1.6rem;
        right:4.5rem;
    }
}
.search-box .input{
    position:relative;
    right:0;
    top:-0.5rem;
    width:100%;
    background:rgba(0, 0, 0, 0.14);
    border:1px solid rgba(255, 255, 255, 0.092);
    border-radius:1.5rem;
    padding:0.4rem 2.5rem 0.4rem 1rem;
    box-sizing:border-box;
    z-index:101;
    color:white;
    font-size:1.15rem;
}
.search-box .input:focus{
    outline:none;
    background:rgba(0, 0, 0, 0.05);
    border:1px solid rgba(255, 255, 255, 0.192);

}
.search-box .btn{
    position:absolute;
    top:-0.5rem;
    right:0.75rem;
    z-index:102;
    font-size:1.1rem;
}
.search-box .sug{
    position:relative;
    top:0;
    left:0.5rem;
    width:96%;
    z-index:100;
    background: rgb(253, 253, 253);
    box-shadow:1px 1px 3px 1px rgba(0,0,0,0.075);
    /* padding:1rem 0.5rem; */
    box-sizing: border-box;
    color:black;
    border-radius:5px;
    padding-bottom:0.5rem;
}
.search-box .head{
    padding:0.25rem;
    color:#aaa;
    font-size:0.9rem;
}
.search-box .sug .item{
    border-bottom:1px solid #e0e0e0a6;
    padding:0.5rem 0.5rem;
    cursor: pointer;
}
.search-box .sug .item:last-child{
    border-bottom:0px solid #e0e0e0a6;
}
.search-box .sug .item:hover{
    background:#00000028;
}