.cart-con {
  display: none;
}
.cart-con.show {
  display: block;
}
.cart {
  position: fixed;
  top: 0rem;
  left: 0;
  width: 100%;
  bottom: 0;
  background: white;
  overflow-y: auto;
  padding-bottom: 1rem;
  z-index: 1;
  transition: unset !important;
}
.cart .gutter {
  position: relative;
  width: 100%;
  height: 1rem;
}
@media screen and (max-width: 841px) and (min-width: 500px) {
  .cart {
    height: 100%;
    left: 15%;
    width: 70%;
    padding-bottom: 5rem;
  }
  .cart-bg {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    background: rgba(0, 0, 0, 0.595);
  }
  .cart .gutter {
    height: 7rem;
    /* background:rgb(49, 55, 61); */
  }
}

.cart .total {
  position: relative;
  top: 5rem;
  height: 10rem;
  background: white;
  padding: 0.2rem 0.25rem;
  border: 1px solid #e0e0e0;
  margin: 0.3rem 0.5rem;
  box-sizing: border-box;
  border-radius: 3px;
  box-shadow: 1px 1px 1px 1px #e0e0e07c;
}
.cart .total h6 {
  margin: 0.2rem 0.25rem;
  border-bottom: 1px solid #e0e0e0;
}
.cart .btn-download {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0rem 0.5rem;
  height: 2rem;
  line-height: 30px;
}
.cart .total .total-child {
  position: absolute;
  top: 2.25rem;
  height: 5.5rem;
  font-size: 1.3rem;
  font-weight: lighter;
  text-align: center;
  margin: 0 0.5rem;
}
.cart .total .total-child span {
  font-size: 4rem;
  display: block;
  line-height: 5rem;
  font-weight: bold;
}
.cart .total .price {
  left: 45%;
  transform: translateX(-100%);
}
.cart .total .price span {
  color: rgb(0, 140, 255);
}
.cart .total .size {
  right: 45%;
  transform: translateX(100%);
}
.cart .total .size span {
  color: rgb(1, 184, 98);
}
.cart .tool {
  position: relative;
  top: 5rem;
  margin: 0 0.5rem;
  height: 2.5rem;
}
.cart .tool-item {
  position: relative;
  width: 33.333%;
  display: inline-block;
  text-align: center;
}
.cart .tool-item button {
  font-size: 10px;
  padding: 0 0.25rem;
}

.cart .tool-item i {
  margin-top: -0.3rem;
}
.cart-list {
  position: relative;
  top: 5rem;
  margin-bottom:5rem;
  padding-bottom:5rem;
}
@media screen and (min-width: 842px) {
  .cart {
    position: absolute;
    top: 0rem;
    right: 0.5rem;
    left: unset;
    height: 100%;
    width: 30%;
    padding-bottom: 5rem;
    background: unset;
    overflow-y: unset;
    max-width:35rem;
  }
  .cart-con {
    display: block;
  }
  .cart .total {
    top: 1.7rem;
  }
  .cart .tool {
    top: 1.7rem;
  }
  .cart-list {
    top: 1.7rem;
  }
}
.cart .empty {
  margin-top: -1rem;
}
.cart .empty .head {
  font-size: 1.5rem;
  color: #aaa;
  font-weight: lighter;
  display: block;
  text-align: center;
  line-height: 3rem;
}
.cart .empty .body {
  position: relative;
  font-size: 0.9rem;
  color: #aaa;
  font-weight: lighter;
  text-align: center;
  width: 90%;
  margin: auto;
}
.cart .settings {
  position: absolute;
  bottom: 0;
  left: 0;
}
.cart .settings .btn {
  padding: 0.1rem 0.25rem 0.25rem 0.25rem;
  background: none;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 2px;
  color: rgb(139, 139, 139);
  cursor: pointer;
}
.cart .settings .btn:hover {
  background: rgba(0, 0, 0, 0.1);
  color: black;
}
.cart .settings .btn:active {
  background: rgba(0, 0, 0, 0.15);
}
.cart .settings .btn:focus {
  outline: none;
}
.cart .settings i {
  position: relative;
  top: 0.2rem;
  margin-right: 0.25rem;
  font-size: 1.2rem;
}
.subtitles {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0.3rem 0.4rem;
  width: 7rem;
}
.subtitles .label {
  position: absolute;
  top: 0.4rem;
  left: 0;
  width: 60%;
}
.subtitles .input-check {
  position: absolute;
  right: 0;
  width: 40%;
}
.actions {
  position: absolute;
  bottom: 0.5rem;
  right: 0.5rem;
}
.select .sec {
  position: absolute;
  width: 50%;
  /* margin-right:1%; */
}
.select .sec.two {
  right: 0;
}
.select label {
  position: relative;
  left: 0.5rem;
  display: block;
}
.select select {
  position: relative;
  left: 0.5rem;
  padding: 0.25rem 0.5rem;
  width: 93%;
  border: 1px solid #e0e0e0;
  border-radius: 5px;
  background: #e0e0e0;
}
.select select:focus {
  background: rgb(250, 250, 250);
}
.cau {
  position: absolute;
  bottom: 0.5rem;
  left: 0.5rem;
  font-size: 0.8rem;
  line-height: 1rem;
  color: #aaa;
  width: 45%;
  text-align: center;
}
@media screen and (min-width: 841px) and (max-width: 1024px) {
  .cart .total.s {
    height: 11.5rem;
  }
  .s .cau {
    bottom: 3rem;
    width: 90%;
  }
}
@media screen and (max-width:842px){
  .cart .total .lock{
    display:none;
  }
}
@media screen and (min-width:842px){
  .cart .total .lock{
    position: absolute;
    left:3.5rem;
    top:0;
    display:block;
  }
  .cart .total .lock i{
    color: rgba(165, 165, 165, 0.801);
    font-size:1.6rem;
  }
  .cart.lock{
    position:fixed;
    top:4.5rem;
    height:80%;
    right:0.2rem;
    padding-top:0;
    padding-right:0;
    overflow-y: auto;
    padding-bottom:10rem;
    z-index:-1;
  }
  .cart.lock::-webkit-scrollbar {
    background: rgba(110, 110, 110, 0);
    width: 0.35rem;
  }
  .cart.lock::-webkit-scrollbar-thumb {
    background: rgba(119, 119, 119, 0.603);
    border-radius: 5px;
  }
  .cart.lock::-moz-scrollbar {
    background: rgba(110, 110, 110, 0);
    width: 0.45rem;
  }
  .cart.lock::-moz-scrollbar-thumb {
    background: rgba(119, 119, 119, 0.603);
    border-radius: 5px;
  }
}
@media screen and (max-height:791px){
  .cart.lock {
    height:70%
  }
}
@media screen and (max-height:550px){
  .cart.lock {
    height:60%
  }
}
@media screen and (max-height:350px){
  .cart.lock {
    height:50%
  }
}
