.cartlist-con{
    position:relative;
    height:2rem;
}
.cartlist-con div{
    transition:all 100ms ease-in-out;
}    
.cart .item{
    position:relative;
    padding:0.2rem 0.25rem;
    border:1px solid #e0e0e0;
    margin:0.3rem 0.5rem;
    box-sizing:border-box;
    border-radius: 3px;
    box-shadow: 1px 1px 1px 1px #ebeaea7c;
    cursor: pointer;
    transition: unset !important;
}
.cart .checking .item{
    width:calc(100% - 3rem);
    left:2rem;
}
.cart .deleting .item{
    width:calc(100% - 3rem);
    left:0;
}
.cart .item:hover{
    background:#eee;
}
.cart .item:active{
    background:#e0e0e0;
}
.cart .item .price{
    position:absolute;
    right:4.5rem;
    color:white;
    text-align:center;
    padding:0 0.2rem;
    background:rgb(0, 140, 255)
}
.cart .item .size{
    position: absolute;
    right:0.25rem;
    width:4.1rem;
    background: rgb(1, 184, 98);
    color:white;
    text-align:center;
}
.cart .checkbox{
    position:absolute;
    top:-0.05rem;
    left:0.25rem;
    height: 1.7rem;
    width:1.7rem;
    background:rgb(207, 207, 207);
    border:1px solid #c2c2c2;
    color:white;
    border-radius:5px;
    padding:0.1rem;
    text-align:center;
    cursor: pointer;
    display: none;
}
.cart .checkbox.check{
    background:rgb(0, 127, 212);
    border:1px solid rgb(0, 127, 212);
}
.cart .checking .checkbox{
    display: block;
}
.cart .delete{
    position:absolute;
    top:0rem;
    right:0.25rem;
    height: 1.7rem;
    width:1.7rem;
    background:rgb(233, 47, 0);
    color:white;
    border-radius:50%;
    padding:0.1rem;
    text-align:center;
    cursor: pointer;
    display:none;
}
.cart .deleting .delete{
    display: block;
}
.cart .num{
    display:inline;
}
.cart .name{
    position:absolute;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    height:1.4rem;
    padding:0;
    width:calc(100% - 8.5rem);
    text-transform: capitalize;
}
.cart .name:hover{
    width:unset;
    width:calc(100%-1rem);
    white-space: unset;
    overflow: unset;
    text-overflow: unset;
    height:unset;
    z-index:1;
}
.cart .name .ot{
    margin-left:1.5rem;
    position: relative;
}
.cart .name .s {
    position: absolute;
    top: 0.17rem;
    display: inline-block;
    margin-left: -1.5rem;
    background: rgba(255, 183, 95, 0.678);
    box-shadow:1px 1px 2px 1px rgba(0, 0, 0, 0.08);
    border: 1px solid rgba(0, 0, 0, 0.445);
    border-radius: 5px;
    box-sizing: border-box;
    color:  rgba(0, 0, 0, 0.514);
    width: 1.1rem;
    height: 1.15rem;
  }
  .cart .name .s b {
    position: absolute;
    top:50%;
    left:50%;
    font-size: 1rem;
    font-weight: 600;
    transform: translateX(-52%) translateY(-50%);
  }