.card {
  position: relative;
  top: 0;
  left: 0;
  width: 98%;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2),
    0 1px 5px 0 rgba(0, 0, 0, 0.12);
  background: #fff;
  cursor: pointer;
  padding: 0.2rem 0.2rem 0.75rem 0.2rem;
  border-radius: 4px;
  overflow: hidden;
  border: 1px solid #eee;
  box-sizing: unset;
  cursor: pointer;
}
.grid-container.small-list .card {
  box-shadow: unset;
  border: 2px solid #eee;
  width: 95%;
  margin: 2rem 0;
  height: 3.3rem;
}
.card .poster {
  position: relative;
  top: 0;
  left: 0;
  padding-bottom: 150%;
  background: lightgray;
}
.grid-container.small-list .card .poster {
  position: absolute;
  top: 0.2rem;
  left: 0.2rem;
  width: 2.5rem;
  display: inline-block;
  padding-bottom: 3.75rem;
  background: lightgray;
}
.card .name {
  position: relative;
  top: 0.5rem;
  font-weight: 500;
  font-size: 1.1rem;
  padding: 0 0 0.5rem 0;
  line-height: 15px;
}
.card .name .actual{
  text-transform:capitalize;
}
/* .card .name .actual span{
  text-transform: none;
} */
.card .name .actual b{
  color:rgb(230, 122, 0);
}
.card .name .s {
  position: relative;
  top: 0.25rem;
  display: inline-block;
  margin-left: 0.5rem;
  margin-bottom:-0.2rem;
  background: rgb(255, 183, 95);
  box-shadow:1px 1px 2px 1px rgba(0, 0, 0, 0.08);
  border: 1px solid rgba(0, 0, 0, 0.445);
  border-radius: 5px;
  box-sizing: border-box;
  color:  rgba(0, 0, 0, 0.514);
  width: 1.2rem;
  height: 1.25rem;
}
.card .name .s b {
  position: absolute;
  top:50%;
  left:50%;
  font-size: 1.4rem;
  font-weight: 500;
  transform: translateX(-50%) translateY(-65%);
}
.grid-container.column .card .name .s {
  top:0.1rem;
}
.grid-container.grid .card .rate{
  top:0;
}
.grid-container.grid .card .name .s{
  position: absolute;
  top:1rem;
  right: 0;
  height:1.15rem
}
.grid-container.grid .card .name .s b{
  transform: translateX(-50%) translateY(-70%);
}
.grid-container.column .card .name {
  height: 2.75rem;
}
.grid-container.grid .card .name span{
  display: none;
}
.grid-container.grid .card .name .plot{
  display: none;
}
.grid-container.small-list .card .name {
  position: absolute;
  left: 3rem;
  top: 0.5rem;
  right: 4rem;
  margin: 0 0.5rem;
  font-size: 1.25rem;
  font-weight: bold;
  line-height: 19px;
  /* background:blue; */
}
.grid-container.small-list .card .name .s{
  top:0rem
}
.grid-container.small-list .card .name span {
  position: relative;
  top: 0;
  left: 0;
  height: 4rem;
}
.card .name .plot {
  display: none;
}
.grid-container.small-list .card .name .plot {
  display: block;
  font-size: 1rem;
  position: relative;
  top: 0.9rem;
  font-weight: 400;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width:calc(100% + 2.25rem)
}
@media screen and (max-width: 768px) {
  .card.multi {
    cursor: unset;
  }
}
@media screen and (max-width: 560px) {
  .grid-container.small-list .card .name {
    position: absolute;
    left: 3rem;
    top: 0.5rem;
    right: 4rem;
    margin: 0 0.5rem;
    font-size: 1.25rem;
    font-weight: bold;
    line-height: 19px;
  }
  .grid-container.small-list .card .name .plot {
    /* display:none; */
  }
}
.card .rate {
  position: relative;
  top: 0.5rem;
  font-weight: 700;
}
.grid-container.small-list .card .rate {
  position: absolute;
  top: 0.5rem;
  bottom: 0.5rem;
  right: -0.5rem;
  margin: 0 1rem;
  display: inline-block;
}
.card .des {
  display: none;
}
.card .rate .star {
  position: relative;
  top: -0.15rem;
  display: inline-block;
}
.card .rate .star i {
  font-size: 0.9rem;
  color: rgb(204, 204, 204);
}
.card .rate .star .light {
  color: rgb(241, 166, 2);
}
.card .rate .num {
  display: inline-block;
  position: absolute;
  top: -0.3rem;
  right: 0;
  color: rgb(117, 117, 117);
}
.grid-container.small-list .card .rate .num {
  position: absolute;
  top: 2rem;
  right: 0;
  display: block;
}
.card .additional {
  display: none;
}
.card .plot-larg {
  display: none;
}
.detail-disable {
  display: none;
}
.card .waiting {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #aaa;
}
.card .waiting i {
  font-size: 2.5rem;
}
.filter-con{
  position: relative;
  margin:auto;

}
.filter{
  position:relative;
  top:1rem;
  left:50%;
  transform: translateX(-50%);
  width:auto;
  text-align:center;
  border-radius:10px;
  overflow: hidden;
  background:rgba(221, 221, 221, 0.44);
  border:1px solid rgba(170, 170, 170, 0.433);
  color:rgba(0,0,0,0.75);
  display: inline-block;
}
.filter .filter-item{
  display: inline-block;
  padding:0.4rem 0.255rem 0.25rem 0.255rem;
  cursor: pointer;
  border-left:1px solid rgba(0, 0, 0, 0);
  border-right:1px solid rgba(0, 0, 0, 0);
}

.filter .filter-item:hover{
  transition: unset !important;
  background:rgba(0, 0, 0, 0.319);
}
.filter .filter-item.active{
  background:rgb(255, 255, 255);
  box-shadow: 0px 0px 6px 2px rgba(0, 0, 0, 0.15);
  border-left:1px solid rgba(0, 0, 0, 0.15);
  border-right:1px solid rgba(0, 0, 0, 0.15);
  color:black;
}
.filter .filter-item.a{
  border-left:unset;
  padding-left:1rem;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}
.filter .filter-item span{
  position: relative;
  top:-0.15rem;
  padding-right:0.75rem;
  border-right:1px solid rgba(170, 170, 170, 0.533);
}
.filter .filter-item.active span{
  border-right:1px solid rgba(170, 170, 170, 0);
}

.filter .filter-item.tv span{
border-right: unset;
padding-right:0.273rem;
}
.filter .filter-item.tv{
  border-right:unset;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}
.filter .filter-item i{
  position: relative;
  top:0.15rem;
  margin-right:0.25rem;
  font-size:1.25rem;
}
.getting-more{
  position: relative;
  width:10rem;
  font-size:1.5rem;
  margin:3rem auto;
}