.card-s-d {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
}
@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.card-s-d .dark-bg {
  position: absolute;
  background: rgba(0, 0, 0, 0.7);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  animation: fade-in 400ms ease-in-out forwards;
}
@keyframes enlarge {
  from {
    transform: scale(0.2);
    opacity: 0.5;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}
.card-s-d .content {
  position: relative;
  top: 2.5rem;
  bottom: -5rem;
  height: 100%;
  width: 40rem;
  margin:auto;
  background: white;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-x:hidden;
  z-index: 10;
  padding-top: 3rem;
  padding-left: 0.25rem;
  box-sizing: border-box;
  color: black;
  animation: enlarge 200ms ease-in-out forwards;
}
.card-s-d .content.pull-up{
  top:0.5rem
}
.card-s-d .content .main {
  position: relative;
  background: white;
  margin: 2rem 0.5rem;
  border: 1px solid #e0e0e0;
  box-shadow: 1px 1px 1px 1px #ebeaea7c;
  padding: 0.5rem;
  border-radius: 5px;
}
.card-s-d .content .main .left {
  position: relative;
  top: 0;
  left: 0;
  width: 23.3rem;
  /* height:4.5rem; */
  display: inline-block;
}
.card-s-d .content .main .left .title {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 1.5rem;
  line-height: 2rem;
  text-transform: capitalize;
}
.card-s-d .content .main .right {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  width: 13rem;
  text-align: center;
  display: inline-block;
}
.card-s-d .content .main .right .head {
  margin-bottom: 1.1rem;
}
.card-s-d .content .main .right .info {
  font-size: 1.75rem;
  color: white;
}
.card-s-d .content .main .right .info span {
  padding: 0rem 0.15rem;
  border-radius: 3px;
}
.card-s-d .content .main .right .info span span {
  position: relative;
  top: -0.25rem;
  margin-left: 0.1rem;
  font-size: 1.1rem;
}
.card-s-d .content .main .right .info .p {
  background: rgb(0, 140, 255);
  margin-right: 0.5rem;
}
.card-s-d .content .main .right .info .s {
  background: rgb(1, 184, 98);
}
.card-s-d .gut {
  position: relative;
  height: 10rem;
  width: 100%;
}
.card-s-d .content .list {
  /* background: red; */
}
.card-s-d .content .top {
  position: relative;
  min-height:2.5rem;
  left: 1rem;
  margin-bottom: 2rem;
}
.card-s-d .content .top .add-cart {
  position: absolute;
  right: 2.5rem;
  border-radius: 5rem;
}
.card-s-d .content .top .red {
  background-color: rgb(230, 50, 0) !important;
}
.card-s-d .content .sn {
  margin: 1rem 0 2rem 0;
}
.card-s-d .content .list .item {
  position: relative;
  padding: 0.2rem 0.25rem;
  border: 1px solid #e0e0e0;
  margin: 0.3rem 0.5rem;
  box-sizing: border-box;
  border-radius: 3px;
  box-shadow: 1px 1px 1px 1px #ebeaea7c;
  cursor: pointer;
}
.card-s-d .content .list .item label {
  cursor: pointer;
}
.card-s-d .content .list .item.episode {
  width: 90%;
  left: 7.5%;
  cursor: pointer;
}
.card-s-d .content .list .item .prices {
  position: absolute;
  right: 4.5rem;
  color: white;
  text-align: center;
  padding: 0 0.2rem;
  background: rgb(0, 140, 255);
}
.card-s-d .content .list .item .sizes {
  position: absolute;
  right: 0.25rem;
  width: 4.1rem;
  background: rgb(1, 184, 98);
  color: white;
  text-align: center;
}
.card-s-d .add-btns{
  position: absolute;
  width:16rem;  
  right:0;
}
@media screen and (min-width: 1000px) {
  .card-s-d .content {
    top: 1rem;
  }
}
@media screen and (max-width: 660px) {
  .card-s-d .content {
    width: 35rem;
  }
  .card-s-d .content .main .left {
    width: 18.3rem;
  }
  .card-s-d .content .main .left .title {
    font-size: 1.7rem;
  }
}
.card-s-d .close {
  display: none;
}
@media screen and (max-width: 840px) {
  .card-s-d .close {
    display: inline;
    position: absolute;
    right: 0.5rem;
    top: 2rem;
    margin: 0.5rem 0;
  }
  .card-s-d .close button {
    color: red;
  }
  .card-s-d .content .main {
    top: 1rem;
  }
  .card-s-d .content {
    top:4rem
  }
  .card-s-d .content.pull-up{
    top:1.5rem
  }
}
@media screen and (max-width: 500px) {
  .card-s-d .content {
    width: 100%;
  }
}

@media screen and (max-width: 470px) {
  .card-s-d .content .main .left {
    display: block;
    margin-bottom: 1rem;
    width: 100%;
  }
  .card-s-d .content .main .left .title {
    margin-bottom: 0.5rem;
  }
  .card-s-d .content .main .right {
    position: relative;
    top: 0;
    left: 0;
    margin: auto;
    width: 14rem;
    left: 50%;
    transform: translateX(-50%);
  }
}
@media screen and (max-width: 380px) {
  .bell-btn{
    position: absolute;
    top:2.75rem;
    right:6.5rem;
  
  }
  .add-btns{
    width:10rem;
  }
  .card-s-d .content .top {
    margin-bottom: 3rem;
  }
}
@media screen and (max-width: 320px) {
  .add-btns{
    right:unset;
    left:0;
    margin-top:0.25rem;
  }
  .bell-btn{
    right: unset;
    left:11rem;
    top:0;
  }
  .card-s-d .content .top {
    left: 0;
    margin-bottom: 4rem;
  }
  .card-s-d .content .top .add-cart {
    right: 0.5rem;
  }
}
@media screen and (max-width: 280px) {
  .card-s-d .content .top button {
    display: block;
  }
  .card-s-d .content .top .add-cart {
    position: relative;
    left: 0;
  }
}
@media screen and (max-width: 250px) {
  .card-s-d .content .item .title span {
    display: none;
  }
}
