.loader {
  position:fixed;
  top: calc(40% + 0.3rem);
  left: 35%;
  margin: -25px 0px 0px -25px;
  display: inline-block;
  width: 50px;
  height: 50px;
  border: 5px solid #e0e0e0;
  border-right: 5px solid #26c6da;
  border-radius: 50%;
  animation: container-rotate 1s infinite cubic-bezier(0.23, 1, 0.455, 0.5);
  z-index:1000;
}

@media screen and (max-width: 841px) {
.loader {
  left:50%;
  width: 40px;
  height: 40px;
  border: 4px solid #e0e0e0;
  border-right: 4px solid #26c6da;
}
}
@keyframes container-rotate {
  to {
    transform: rotate(360deg);
  }
}

