.nav-o {
  position: absolute;
  top: 0;
  left: 0;
  width: 2rem;
  padding: 0 0.5rem;
  bottom: 0;
}
.search-o {
  position: absolute;
  top: 0;
  left: 3rem;
  right: 2rem;
  bottom: 0;
}
.more-o {
  position: absolute;
  right: 0;
  top: 0;
  width: 1rem;
  bottom: 0;
  padding: 0.5rem;
}
.hide-extra .mdl-layout__tab-bar-container {
  display: none;
}
.shopping-cart{
 display:none;
 margin-right:0.5rem;
}
@media screen and (max-width: 850px) {  
.shopping-cart{
  display:block
}
}
@media screen and (max-width: 600px) {
  .show-for-search .mdl-layout__tab-bar-container {
    display: block;
  }
}
@keyframes back {
  from {
    transform: rotate(180deg);
  }
  to {
    transform: rotate(0deg);
  }
}
.header-back {
  position: absolute;
  top: 0.3rem;
  left: 0.3rem;
  padding: 0.85rem 0.85rem 0.7rem 0.85rem;
  z-index: 5;
  color: white;
  background: #455a64;
  cursor: pointer;
  border: none;
}
.header-back i.rotate {
  animation: back 300ms forwards;
}
@media screen and (min-width: 500px) {
  .title.hide {
    display: block;
  }
}
@media screen and (max-width: 1021px) {
  .header-back {
    top: 0.4rem;
    left: 0.4rem;
  }
}
@media screen and (min-width: 1021px) {
  .header-back {
    top: 0.6rem;
    left: 0.85rem;
  }
