.btn-corner {
  position: fixed;
  bottom: 5%;
  right: 5%;
  z-index: 2;
  transition: bottom 300ms ease-in-out;
}
.btn-corner.btn-rise {
  bottom: 5%;
}
.btn-corner.btn-drop {
  bottom: -5rem;
}
@keyframes glow {
  0% {
    opacity: 1;
    border-radius: 50%;
  }
  70% {
    opacity: 1;
  }
  100% {
    width: 150rem;
    height: 150rem;
    opacity: 0;
    border-radius: 50%;
  }
}
.btn-corner .glow {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 1%;
  height: 1%;
  background: rgb(244, 248, 252);
  border-radius: 50%;
  transform: translate(-50%, -50%);
}
.btn-corner .glow.show {
  animation: glow 400ms;
}
.btn-itself {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2),
    0 1px 5px 0 rgba(0, 0, 0, 0.12);
  text-shadow: 2px 1px 3px rgba(0, 0, 0, 0.111);
}
.btn-itself.download-color {
  background:rgb(76, 0, 255) !important;
}
.getback-con {
  display: none;
}
@media screen and (min-width: 841px) { 
    .btn-corner {
    display: none;
  }
 .getback-con {
    display: block;
    position: fixed;
    bottom: 5%;
    right: 12.5%;
    transition: bottom 300ms ease-in-out;
    cursor: pointer;
  }

  .getback-con.get-back-rise {
    bottom: 5%;
  } 
  .getback-con.get-back-drop {
    bottom: -15%;
  }
  .getback-con .curt {
    position: absolute;
    top: -1.05rem;
    left: 1rem;
    width: 2rem;
    height: 2rem;
    text-align: center;
    background: #0b4047;
    border-radius: 50%;
    color: white;
  }
  .getback-con button {
    background: #0b4047 !important;
  }
}
