.card-detail {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.85);
  z-index: 1;
  color: white;
  padding: 5%;
  cursor: default;
}
.column .card-detail {
  font-size: 1.2rem;
  font-weight: bold;
  letter-spacing: 1px;
}
.small-list .card-detail {
  padding: 1%;
}
.gird .card-detail .head {
  height: 25%;
}
.column .card .card-detail .head {
  height: 10%;
}
.card-detail .body {
  position: relative;
  top: 10%;
}
.card-detail .body div {
  text-align: center;
  font-weight: normal;
  position: absolute;
  padding: 5%;
}
.small-list .card-detail .body {
  position: absolute;
  top: unset;
  bottom: 0;
  left: 1%;
  width: 10rem;
  height: 50%;
}
.small-list .card-detail .body div {
  padding: 2%;
}
.card-detail .price {
  left: 0;
  background: rgb(0, 140, 255);
}
.card-detail .size {
  right: 0;
  background: rgb(1, 184, 98);
}
.grid .card-detail .price,
.grid .card-detail .size {
  top: -1rem;
  width: 100%;
  right: unset;
  left: unset;
  margin: auto;
  padding: 0;
}
.grid .card-detail .size {
  top: 0.5rem;
}
.card-detail .foot {
  position: absolute;
  bottom: 3rem;
  right: 2%;
  left: 0;
}
.small-list .card-detail .foot {
  left: unset;
  height: 2.5rem;
  bottom: 50%;
  transform: translateY(50%);
  right: 1%;
  width: 20rem;
}
.card-detail .foot button {
  position: absolute;
  top: 0;
  color: white;
  font-size: 0.9rem;
  padding: 0 1rem;
  text-shadow: 2px 1px 3px rgba(0, 0, 0, 0.211);
}
.card-detail .foot button i {
  margin-right: 0.3rem;
}

.card-detail .foot .see-more {
  left: 0;
}
.card-detail .foot .add-cart {
  right: 0;
  border-radius: 5rem;
}
.card-detail .foot .red-btn {
  background: rgb(233, 47, 0);
}
.column .card-detail .foot .see-more i {
  display: none;
}
@media screen and (max-width: 1330px) and (min-width: 841px) {
  .column .card-detail .foot .see-more {
    top: -2.7rem;
    left: 50%;
    width: 11rem;
    transform: translateX(-50%);
  }
  .column .card-detail .foot .add-cart {
    right: 50%;
    width: 11.5rem;
    transform: translateX(50%);
  }
  .small-list .card-detail .foot .see-more {
    top: 0;
    left: 0;
    width: unset;
    transform: unset;
  }
  .small-list .card-detail .foot .add-cart {
    top: 0;
    right: 0;
    width: unset;
    transform: unset;
  }
}
@media screen and (max-width: 600px) and (min-width: 500px) {
  .column .card-detail .foot .see-more {
    top: -2.7rem;
    left: 50%;
    width: 11rem;
    transform: translateX(-50%);
  }
  .column .card-detail .foot .add-cart {
    right: 50%;
    width: 11.5rem;
    transform: translateX(50%);
  }
  .small-list .card-detail .foot .see-more {
    top: 0;
    left: 0;
    width: unset;
    transform: unset;
  }
  .small-list .card-detail .foot .add-cart {
    right: 0;
    width: unset;
    transform: unset;
  }
}
@media screen and (max-width: 300px) {
  .column .card-detail .foot .see-more {
    top: -2.7rem;
    left: 50%;
    width: 11rem;
    transform: translateX(-50%);
  }
  .column .card-detail .foot .add-cart {
    right: 50%;
    width: 11.5rem;
    transform: translateX(50%);
  }
  .small-list .card-detail .foot .see-more {
    top: 0;
    left: 0;
    width: unset;
    transform: unset;
  }
  .small-list .card-detail .foot .add-cart {
    right: 0;
    width: unset;
    transform: unset;
  }
}
@media screen and (min-width: 460px) {
  .small-list .card-detail .foot .see-more i {
    display: none;
  }
}
@media screen and (max-width: 460px) {
  .small-list .card-detail .foot i {
    text-align: center;
    font-size: 1.4rem;
  }
  .small-list .card-detail .foot {
    width: 6rem;
    transform: unset;
    bottom: 15%;
  }
  .small-list .card-detail .foot button {
    color: white;
    font-size: 0.9rem;
    text-shadow: 2px 1px 3px rgba(0, 0, 0, 0.211);
    height: 40px;
    min-width: 40px;
    width: 40px;
    border-radius: 50%;
    padding: 0;
    overflow: hidden;
    box-shadow: 0 1px 1.5px 0 rgba(0, 0, 0, 0.12),
      0 1px 1px 0 rgba(0, 0, 0, 0.24);
    line-height: normal;
  }
  .small-list .card-detail .foot .see-more i {
    margin-right: 0.1rem;
    display: block;
  }
  .small-list .card-detail .foot .see-more {
    margin-right: 1rem;
    background: rgba(158, 158, 158, 0.2);
  }
  .small-list .card-detail .foot .see-more span {
    display: none;
  }
  .small-list .card-detail .foot .add-cart span {
    display: none;
  }
  .small-list .card-detail .foot button i {
    margin-right: 0rem;
  }
}

.grid .card-detail .foot .see-more span {
  display: none;
}
.grid .card-detail .foot .add-cart span {
  display: none;
}

.small-list .card-detail .body {
  width: 8rem;
}
.small-list .card-detail .body .size {
  position: absolute;
  left: 4rem;
}
