.sector .item.input{
    width: calc(100% - 9rem);
    margin-right:0.5rem;
    padding-right:0.5rem;
}
.sector .select{
    width: 8.5rem;
    text-align:center;
    padding: 0.5rem 0.5rem;
    /* background:rgba(100, 100, 100, 0.171); */
    background:rgba(100, 100, 100, 0.13);
    border:1px solid rgba(170, 170, 170, 0.2);
    border-radius: 5px;
    font-size: 1.05rem;
    cursor: pointer;
    box-sizing: border-box;
    /* color:black; */
    font-weight: lighter;
}
.sector .select option{
    text-align:center;
    margin:0;
    color:black;
    background:rgba(100, 100, 100, 0.171);
}
.sector .input-con{
    width:100%;
}