.cat {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
}
.cat .navigator {
  position: relative;
  top: 1rem;
  left: 1%;
  width: 98%;
  padding: 0.75rem 0.25rem 0.75rem 2rem;
  box-sizing: border-box;
  background: rgb(238, 238, 238);
  border: 1px solid #cfcfcf;
  font-size: 1.2rem;
  border-radius: 7px;
  text-align: left;
  cursor: pointer;
  font-weight: 500;
}
.cat .navigator i {
  position: absolute;
  top: 0.8rem;
  left: 8rem;
}
.cat .lower {
  position: relative;
  top: 1.5rem;
}
.cat .sector {
  padding: 1rem 0;
  position: relative;
  top: 1.5rem;
  left: 10%;
  width: 80%;
  text-transform:capitalize;
}
@media screen and (max-width: 500px) {
  .cat .sector {
    width: 90%;
    left: 5%;
  }
}
.cat .sector .item {
  position: relative;
  padding: 0.5rem calc(10% + 1rem);
  font-size: 1.1rem;
  left: 0;
  width: 100%;
  margin: 1rem 0;
  border-radius: 5px;
  cursor: pointer;
  box-sizing: border-box;
}
.cat .sector .item.input {
  background:rgba(100, 100, 100, 0.13)
}
.cat .sector .item.input:hover {
  background:rgba(80, 80, 80, 0.199)
}
.cat .sector .item.input:focus {
  background:rgba(126, 126, 126, 0.199)
}
@keyframes squish {
  from {
    margin-bottom: 20rem;
  }
  /* to{margin-bottom:1.25rem;} */
}
.cat .sector .item {
  animation: squish 750ms cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards;
  border:1px solid rgba(170, 170, 170, 0.2);
}
.cat .sector .item span {
  position: absolute;
  right: calc(10% + 1rem);
  text-align: right;
}
.cat .sector .item i {
  position: absolute;
  left: 1rem;
  text-align: left;
}
.cat .sector .item.light {
  background: rgb(245, 245, 245);
}
.cat .sector .item.dark {
  background: rgb(230, 230, 230);
}
.cat .sector .item:hover {
  background: #aaa;
}
.cat .gutter{
  position:relative;
  height:1.5rem;
}