
.grid-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  transition: unset !important;
}
@media screen and (max-width: 841px) {
  .grid-container .gutter {
    position: relative;
    height: 5rem;
  }
}
.grid-container .col.phone-1 {
  position: relative;
  top: 0;
  left: 0;
  width: 30%;
  margin: 2rem 0 0 2%;
  display: inline-block;
}
.grid-container.column .col.phone-1 {
  width: 95%;
}

.grid-container .col.phone-2 {
  position: relative;
  top: 0;
  left: 0;
  width: 62.5%;
  margin: 2rem 0 0 2%;
  display: inline-block;
}
.grid-container .col.phone-3 {
  position: relative;
  top: 0;
  left: 0;
  width: 95%;
  margin: 2rem 0 0 2%;
  display: inline-block;
}
/* tablet viewwwwwwww */
@media screen and (max-width: 841px) and (min-width: 500px) {
  .grid-container .col.tab-1 {
    position: relative;
    top: 0;
    left: 0;
    width: 14.2%;
    margin: 2rem 0 0 2%;
    display: inline-block;
  }
  .grid-container.column .col.tab-1 {
    width: 46.3%;
  }
  .grid-container .col.tab-2 {
    position: relative;
    top: 0;
    left: 0;
    width: 30%;
    margin: 2rem 0 0 2%;
    display: inline-block;
  }
  .grid-container .col.tab-3 {
    position: relative;
    top: 0;
    left: 0;
    width: 46.3%;
    margin: 2rem 0 0 2%;
    display: inline-block;
  }
  .grid-container .col.tab-4 {
    position: relative;
    top: 0;
    left: 0;
    width: 63%;
    margin: 2rem 0 0 2%;
    display: inline-block;
  }
  .grid-container .col.tab-5 {
    position: relative;
    top: 0;
    left: 0;
    width: 79%;
    margin: 2rem 0 0 2%;
    display: inline-block;
  }
  .grid-container .col.tab-6 {
    position: relative;
    top: 0;
    left: 0;
    width: 96%;
    margin: 2rem 0 0 2%;
    display: inline-block;
  }
}
@media screen and (max-width: 841px) {
  .grid-container.small-list {
    width: 100%;
  }
  .grid-container.small-list .col {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    margin: 2rem 0 0 2%;
    display: block;
    height: 3rem;
  }
}
@media screen and (min-width: 841px) {
  .grid-container {
    width: 70%;
  }
  .grid-container.grid .col.desk-1 {
    position: relative;
    top: 0;
    left: 0;
    width: 14.2%;
    margin: 2rem 0 0 2%;
    display: inline-block;
  }
  .grid-container.grid.lock .col.desk-1 {
    width: 14.4%;
  } 
  .grid-container.column .col.desk-1 {
    position: relative;
    top: 0;
    left: 0;
    width: 30.6%;
    margin: 2rem 0 0 2%;
    display: inline-block;
  }
  .grid-container.column.lock .col.desk-1 {
    width: 31%;
  } 
  .grid-container.small-list .col.desk-1 {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    margin: 2rem 0 0 2%;
    display: block;
    height: 3rem;
  }
  .grid-container.small-list.lock .card {
    width: 96%;
  }
}
.hide{
  display: none !important;
}
@media screen and (min-width:842px){
  .grid-container.lock {
    position:fixed;
    top:4.5rem;
    height:80%;
    padding-bottom:10rem;
    overflow-y: auto;
    width:69%;
    padding-top: 1.5rem;
  }
  .grid-container.lock::-webkit-scrollbar {
    background: rgba(110, 110, 110, 0);
    width: 0.35rem;
  }
  .grid-container.lock::-webkit-scrollbar-thumb {
    margin-right:10rem;
    background: rgba(119, 119, 119, 0.603);
    border-radius: 5px;
  }
  .grid-container.lock::-moz-scrollbar {
    background: rgba(110, 110, 110, 0);
    width: 0.35rem;
  }
  .grid-container.lock::-moz-scrollbar-thumb {
    background: rgba(119, 119, 119, 0.603);
    border-radius: 5px;
  }
}

@media screen and (max-height:791px){
  .grid-container.lock {
    height:70%
  }
}
@media screen and (max-height:550px){
  .grid-container.lock {
    height:60%
  }
}
@media screen and (max-height:350px){
  .grid-container.lock {
    height:50%
  }
}
