.app{
  position: fixed;
  top:0;
  left:0;
  right:0;
  bottom:0;
}
.header-o{
  position:absolute;
  top:0;
  left:0;
  right:0;
  height:3rem;
  background:rgb(0, 103, 199);
  box-shadow:0px 1px 10px 1px rgba(0, 0, 0, 0.253);
}
.body-o{
  position: absolute;
  top:3rem;
  right:0;
  left:0;
  bottom:0;
}
.side-bar-o{
  position:absolute;
  top:0;
  bottom:0;
  left:-100%;
  width:50%;
  background:white;
  box-shadow:1px 0px 10px 1px rgba(0, 0, 0, 0.274);
}
.mdl-layout{
  position:absolute;
}
body div{
  transition: all 500ms cubic-bezier(0.175, 0.885, 0.42 , 1.185)  !important;
  /* overflow-x: hidden; */
}
body .mdl-menu.is-animating {
  transition: opacity 0.2s cubic-bezier(0.4, 0, 0.2, 1), clip 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
body .mdl-menu__outline {
  background:rgba(0,0,0,0);
  transition: unset !important;
}
body .mdl-layout__drawer {
  transition: all 400ms cubic-bezier(0.68, -0.55, 0.265, 1.55)  !important;
}
.mdl-badge[data-badge]:after {
  background:#f0574c;
}
.warn-con{
  position: absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
  background:rgba(0, 0, 0, 0.733);
  z-index:5;
}
@keyframes slide{
  0%{
    top:-5rem;
  }
  50%{
    top:5rem;
  }
  100%{
    top:1rem;
  }
}
.warn-con .warn-msg{
  position: relative;
  max-width:20rem;
  width:100%;
  margin:auto;
  top:1rem;
  background:rgb(255, 233, 174);
  padding:1rem;
  border-radius:5px;
  box-shadow:1px 1px 5px 2px rgba(0, 0, 0, 0.253);
  animation:slide 1s forwards cubic-bezier(0.175, 0.885, 0.42 , 1.185);
  font-size:1rem;
}
.warn-con .warn-msg button{
  margin-left:1rem;
  /* margin:auto; */
}




