.mdl-list__item:hover{
    background: rgba(0,0,0,0.3);
    cursor: pointer;
}
.mdl-list__item:active{
    background-color:#e0e0e071;
}
.option .hide{
    visibility: hidden;
}
.option .mdl-list__item-secondary-content{
    position: relative;
    width:2.6rem;
    margin:0;
}
.option .mdl-list__item{
    position: relative;
}
.option .fake-click-area{
    position:absolute;
    left:0;
    width:100%;
    height:100%;
    z-index:0;
}