@keyframes up{
    from{top:15rem}
    to{top:5rem}
}
.selection{
    position:relative;
    top:5rem;
    margin:auto;
    width:100%;
    padding:0 10%;
    box-sizing: border-box;
    animation:up 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
/* cubic-bezier(0.6, -0.28, 0.735, 0.045) */
/* cubic-bezier(0.175, 0.885, 0.32, 1.275) */
.selection .box{
    position: relative;
    display:inline-block;
    width:30%;
    height:15rem;
    border:1px solid #e0e0e0;
    border-radius:5px;
    padding:0.215rem;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.07), 0 3px 1px -2px rgba(0, 0, 0, 0.1),
    0 1px 5px 0 rgba(0, 0, 0, 0.1);
    margin-right:2%;
    text-align:center;
    margin-bottom:2rem;
    transition:unset !important;
}
.selection .box.sea{
    margin-right:0;
}
.selection .box .icon{
    position:relative;
    top:0;
    left:0;
    width:100%;
    height:70%;
}
.selection .box .icon i{
    font-size:6rem;
    position:relative;
    top:50%;
    transform: translateY(-50%);
}
.selection .box.genre .icon{
    color:rgb(0, 92, 145);
}
.selection .box.time .icon{
    color:rgb(0, 145, 92);
}
.selection .box.sea.s .icon{
    color:rgb(107, 87, 61);
}
.selection .box.actors .icon{
    color:rgb(85, 48, 94);
}
.selection .box.production .icon{
    color:rgb(61, 100, 107);
}
.selection .box.language .icon{
    color:rgb(172, 107, 33);
}
.selection .box .text{
    position:relative;
    top:0;
    left:0;
    width:100%;
    height:30%;
    font-size:1.2rem;
}
.selection .box .text span{
    color:#aaa;
}
.selection .box .text div{
    font-weight:bold;
    font-size:2rem;
    margin-bottom:0.5rem;
}
.selection .box:hover{
    cursor: pointer;
    background: #eee;
}
@media screen and (min-width:1500px){
.selection{
    padding:0 20%;
}
}
@media screen and (max-width:1070px) and (min-width:490px){
    .selection{
        width:100%;
        padding:0 5%;
    }
    /* .selection .box.time{
        margin-right:0;
    } */
    /* .selection .box.sea{
        position:relative;
        left:50%;
        transform: translateX(-50%);
} */
}
@media screen and (max-width:490px){
    .selection{
        top:2rem;
        width:100%;
        padding:0 1%;
    }
    .selection .box{
        height:10rem;
        margin-right:2%;
    }
    .selection .box.sea{
        margin-right:0;
    }
.selection .box .icon i{
    font-size:4rem;
}
.selection .box .text div{
    font-size:1.5rem;
}
.selection .box .text{
    font-size:1rem;
}
.selection .box .icon{
    height:60%;
}
}
@media screen and (max-width:410px){
    .selection{
        margin:auto;
    }
    .selection .box{
        width:43%;
        margin-right:2%;
        margin-left: 2%;
    }
    .selection .box.sea{
        margin-right:2%;
    }
}
@media screen and (max-width:275px){
    .selection{
        margin:auto;
    }
    .selection .box{
        width:93%;
        margin-right:2%;
        margin-left: 2%;
    }
    .selection .box.sea{
        margin-right:2%;
    }
}
