@keyframes right{
  0%{
    left:0;
    opacity: 1;
  }
  25%{
    left:20rem;
    opacity: 0;
  }
  50%{
    left:-20rem;
    opacity: 0;
  }
  100%{
    left:0;
    opacity: 1;
  }
}
@keyframes left{
  0%{
    left:0;
    opacity: 1;
  }
  25%{
    left:-20rem;
    opacity: 0;
  }
  50%{
    left:20rem;
    opacity: 0;
  }
  100%{
    left:0;
    opacity: 1;
  }
}
.movie-d {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  max-width: 400px;
  margin: 0 auto;
  /* background:rgb(21, 21, 26); */
  /* color:white; */
}

.movie-d .poster {
  position: relative;
  top: 0.3rem;
  width: 98%;
  left:0rem;
  /* background:red !important; */
  /* margin: auto; */
  border: 2px solid white;
  padding-bottom: 150%;
  background: lightgray;
  margin:0;
}
.right .poster{
  animation: left 500ms ease-in-out forwards;
}
.left .poster{
  animation: right 500ms ease-in-out forwards;
}
@media screen and (min-width: 400px) {
  .movie-d .poster {
    width: 400px;
  }
}
.movie-d .pag {
  position: absolute;
  top: 13rem;
  left: 0;
  right: 0;
  height:2rem;
  max-width: 510px;
  margin: 0 auto;
}
@media screen and (min-width: 544px) {
.movie-d .pag {
    left: -15%;
  right: -16%;
}
}

.movie-d .pag i {
  font-size: 3.5rem;
}
.movie-d .pag button {
  position: absolute;
  top: 0;
  padding: 0rem;
  background: rgba(0, 0, 0, 0.2);
  cursor: pointer;
  border: 1px solid rgb(105, 105, 105);
  color: rgb(105, 105, 105);
  border-radius: 10px;
}
.movie-d .pag button:focus {
  outline: none;
}
.movie-d .pag div:active {
  background: rgba(0, 162, 255, 0.432);
}
.movie-d .pag .back {
  left: 0;
}
.movie-d .pag.disable-left .back {
  border: 1px solid rgba(82, 43, 43, 0.5);
  background: rgba(255, 0, 0, 0.1);
  color:  rgba(180, 67, 67,0.6);
}
.movie-d .pag .forward {
  right: 0;
}
.movie-d .pag.disable-right .forward {
  border: 1px solid rgba(82, 43, 43, 0.5);
  background: rgba(255, 0, 0, 0.1);
  color:  rgba(180, 67, 67,0.6);
}
.movie-d .details {
  position: relative;
  top: 2rem;
  max-width: 400px;
  width: 90%;
  margin: 0 auto;
  padding-bottom: 2rem;
}
.movie-d .item{
  margin:0.5rem 0;
  padding:0.7rem;
  border-radius: 10px;
  background:rgb(245, 245, 245);
}
.movie-d .item .header{
  border-bottom:1px solid #ddd;
  padding-bottom:0.25rem;
}
.movie-d .uppercase{
  text-transform: capitalize;
}
.movie-d .details li {
  line-height: 1.6rem;
  padding:0rem 1rem;
  margin: 0.25rem 0;
  min-height: unset;
}
/* .movie-d .details li:hover{
  background:rgba(238, 238, 238, 0.15);
} */
.movie-d .details ul {
  padding: 0.25rem 0;
  margin-top:0.5rem !important;
}
.movie-d .details h5 {
  margin: 0;
}
.movie-d .details .rated {
  background: rgb(255, 145, 0);
  padding: 0.2rem 0.5rem;
  border-radius: 5px;
  width:4rem;
  text-align:center
}
.movie-d .details .rating {
  background: rgb(0, 172, 57);
  padding: 0.2rem 0.5rem;
  border-radius: 5px;
}
.movie-d .details .center {
  position: relative;
  top: -1rem;
  left:0;
  display:inline-block;
}
.movie-d .waiting {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #aaa;
}
.movie-d .waiting i {
  font-size: 5rem;
}

.movie-d .add-to-cart{
    position:absolute;
    top:-1rem;
    right:0;
    z-index:1
}
.movie-d .red{
    background-color:rgb(230, 50, 0) !important;
}
.movie-d  .add-to-cart button{
    font-size: 0.9rem;
    padding: 0 1rem;
    text-shadow: 2px 1px 3px rgba(0, 0, 0, 0.211);
    border-radius: 5rem;
}
.movie-d .add-to-cart button.make-yellow{
  background:#df8f26;
}
.right .details{
  animation: left 500ms ease-in-out forwards  ;
}
.left .details{
  animation: right 500ms ease-in-out forwards;
}

@keyframes rightBig{
  0%{
    left:29.5;
    opacity: 1;
  }
  25%{
    left:9.5rem;
    opacity: 0;
  }
  50%{
    left:49.5rem;
    opacity: 0;
  }
  100%{
    left:29.5;
    opacity: 1;
  }
}
@keyframes leftBig{
  0%{
    left:29.5;
    opacity: 1;
  }
  25%{
    left:49.5rem;
    opacity: 0;
  }
  50%{
    left:9.5rem;
    opacity: 0;
  }
  100%{
    left:29.5;
    opacity: 1;
  }
}
@media screen and (min-width: 1210px) {
  .movie-d{
    top:0.5rem;
    left:-39.5%;
  }
  .movie-d .details{
    position:absolute;
    top:1rem;
    left:29.5rem;
    max-width:320px;
  }
  .right .details{
    animation: rightBig 500ms ease-in-out forwards;
  }
  .left .details{
    animation: leftBig 500ms ease-in-out forwards;
  }
  
.movie-d .pag {
  max-width:60rem;
  z-index: 1;
  left:-3.75rem;
  width:73.5rem;
  /* background:red; */
}
}
@media screen and (min-width: 1300px) {
  .movie-d .details{
    max-width:400px;
  }
  .movie-d .pag {
    max-width:63rem;
    left:-3.9rem;
  }
}
.movie-d .header{
  position: relative;
}