.box-office{
    position: relative;
    top:0;
    left:0;
    width:100%;
    height:100%;
}
.box-office .box{
    position:relative;
    top:2rem;
    left:5%;
    width:90%;
    height: 26rem;
    box-sizing: border-box;
    padding:0.2rem;
    border:1px solid #ddd;
    border-radius:5px;
    margin-bottom:2rem;
}
.box-office .poster{
    position:relative;
    top:0;
    left:0;
    width:30%;
    height: 100%;
    display:inline-block;
}
@media screen and (max-width:940px) and (min-width:841px){
    .box-office .box{
        height: 20rem;
    }
}
.box-office .poster img{
    position:absolute;
    top:0;
    left:0;
    height:100%;
    width:auto;
}
.box-office .info{
    position:absolute;
    top:1rem;
    right:0.5rem;
    width:55%;
    height:100%;
}
.box-office .info .title{
    font-size:2rem;
    margin-bottom:6rem;
    background:white;
}
.box-office .info .title .rank{
    position: absolute;
    top:0;
    left:0;
    width:3rem;
}
.box-office .info .title .name{
    position: absolute;
    top:0;
    left:3rem;
    line-height: 2rem;
}
.box-office .info .bold-des span{
    font-weight:bold;
    font-size:1.1rem;
}
.box-office .info .bold-des{
    margin-bottom:1rem;
}
.box-office .star {
  position: relative;
  top:-0.15rem;
  display: inline-block;
}
.box-office .star i{
  font-size:1.75rem;
  color:rgb(204, 204, 204);
}
.box-office .star .light{
  color:rgb(241, 166, 2);
}
.box-office .info .rating{
    position: absolute;
    bottom:2rem;
    width:100%;
    right:0;
}
.box-office .info .rating span{
    font-size:1.1rem;
    font-weight:bold
}
.box-office .info .rating .rate{
    position:absolute;
    right:0;
}
.box-office .info .rating .star{
    top:0rem;
}

@media screen and (max-width:660px){
    .box-office .box{
        position: relative;
        height:unset;
        left:1%;
        width:48%;
        margin-right:1%;
        display:inline-block;
    }
    .box-office .poster{
        position:relative;
        width:100%;
    }
    .box-office .poster img{
        position: relative;
        width:100%;
        height:auto;
    }
    .box-office .info{
        position:relative;
        top:0.5rem;
        left:0.2rem;
        border:2px solid rgba(255, 0, 0, 0);
        width:100%;
        height:22rem;
        margin-bottom:1rem;
        box-sizing: border-box;
    }
    .box-office .info .title{
        font-size:1.5rem;
    }

}
@media screen and (max-width:495px){
    .box-office .box{
        left:2.5%;
        width:95%;
        margin-right:unset;
    }
}
@media screen and (max-width:450px){
    .box-office .box{
        left:5%;
        width:90%;
    }
}
@media screen and (max-width:350px){
    .box-office .box{
        left:2.5%;
        width:95%;
    }
}
@media screen and (max-width:300px){
    .box-office .box{
        left:0;
        width:100%;
    }
}
@media screen and (min-width:1500px){
    .box-office .box{
        width:70%;
        margin:auto;
        margin-bottom:2rem;
    }
}

