.empty-search {
  position: relative;
  top: 3rem;
  left: 50%;
  transform: translateX(-50%);
  margin: auto;
  color: rgb(139, 139, 139);
  text-align: center;
  font-size: 1.3rem;
}
.empty-search .low {
  font-size: 1rem;
  color: #aaa;
  margin:0 0 1rem 0;
}

.empty-search .i {
  font-size: 5rem;
}

.result-header {
  position: relative;
  top: 1rem;
  font-size: 1.7rem;
  text-align: center;
  width: 80%;
  margin: 0 auto;
  height: 2rem;
  font-weight:300;
}
@media screen and (max-width: 841px) {
  .result-header {
    width:95%;
    font-size:1.5rem;
    margin-bottom: 0; 
  }
}
.result-header .txt {
  position: relative;
  max-width: 97%;
  padding: 0 0 1rem 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block;
}
.result-header .cot {
  position: relative;
  top: -1.1rem;
}
.empty-search button{
  transition: all 500ms ease-out;
}
.empty-search .add-done{
  background:rgb(129, 236, 147) !important;
}
