.more-apps{
    position: relative;
}
.more-apps .con{
    position: relative;
    top:5rem;
    width:29rem;
    margin:auto;
}
@media screen and (max-width:450px){
.more-apps .con{
    width:95%;
}
}

.more-apps .con .link{
    position: relative;
    padding:0.5rem;
    border-radius:5px;
    margin-bottom:2rem;
    background: white;
    border:1px solid rgba(170, 170, 170, 0.514);
    box-shadow: 0 2px 5px 0 rgba(124, 124, 124, 0.1), 0 3px 1px -2px rgba(54, 54, 54, 0.05),
    0 1px 5px 0 rgba(78, 78, 78, 0.05);
    height:5rem;
    cursor: pointer;
    color:black;
    font-weight: normal;
    transition: unset !important;
} 
@media screen and (max-width:300px){
    .more-apps .link{
        height:7rem !important
    }
    }
    @media screen and (max-width:260px){
        .more-apps .link{
            height:9rem !important
        }
        }
        @media screen and (max-width:260px){
            .more-apps .link{
                height:11rem !important
            }
            }
.more-apps .con .link div{
    transition: unset !important;
}
.more-apps .con .link i{
    position: relative;
    top:0.75rem;
    left:0.5rem;
    font-size:3rem;
}
.more-apps .con .link.a i{
    color:rgb(0, 167, 0);
}
.more-apps .con .link.a.enter i{
    color:white;
}
.more-apps .con .link.w i{
    color:rgb(0, 117, 185);
}
.more-apps .con .link.a:hover{
    background:rgb(0, 167, 0) !important;
    color:white !important;
}
.more-apps .con .link.w:hover{
    background:rgb(0, 117, 185) !important;
    color:white !important;
}
.more-apps .con .link .text{
    position:absolute;
    top:0.5rem;
    left:5rem;
    right:0.5rem;
}
.more-apps .con .link .text .head{
    font-size:1.5rem;
    position: relative;
    top:0.5rem;
}
.more-apps .con .link .text .des{
    position: relative;
    top:1rem;
}

