
.toast{
  position:fixed;
  left:50%;
  transform:translateX(-50%);
  bottom:-10rem;
  background:#3f3f3f;
  padding:1rem 1.5rem;
  font-size:1.2rem;
  color:white;
  font-weight:400;
  display:inline-block;
  z-index:5;
  min-width:19rem;
  max-width:25rem;
  text-align:center;
  box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.116);
  transition:bottom 300ms ease-in-out;
  letter-spacing: 1px;
  height:1.5rem;
  z-index:1000000000;
}
.toast .message{
  position:absolute;
  left:0;
  right:0;
  display:inline-block;
  font-weight:400;
}
.toast .message.with-action{
  right:5rem;
}
.toast .action{
  position: absolute;
  top:0;
  right:0;
  bottom:0;
  padding:0 1rem;
  border:none;
  background:none;
  color:rgba(255, 255, 255, 0.6);
  font-size:14px;
  font-weight:600;
  text-transform: uppercase;
  border-radius:0;
  height:unset;
}
.toast .action:hover{
  background:rgba(255, 255, 255, 0.07);
  color:white;
}
.toast .action:active{
  background: rgba(255, 255, 255, 0.15)
}
.toast .action:focus{
  outline:none;
  color:white;
}
.toast.show-toast{
  bottom:5%;
}
.toast.error{
  border-left:5px solid rgb(240, 0, 0);
}
.toast.warnning{
  border-left:5px solid rgb(240, 176, 0);
}
@media screen and (max-width: 310px) {
  .toast{
    min-width:100%;
  }
}
