.please{
    position:fixed;
    top:0;
    left:0;
    width:100%;
    height:100%;
    z-index:200;
}
.please-con{
    position:absolute;
    top:50%;
    left:50%;
    transform:translate(-50%,-50%);
    background:white;
    border:1px solid rgba(255, 161, 161, 0.315);
    border-radius:5px;
    box-shadow:2px 2px 10px 1px #20202042;
    width:20rem;
    padding:2rem;
    padding-bottom:4rem;
    text-align:center;
    font-size:1.4rem;
    letter-spacing: 1px;
    line-height: 1.8rem;
    font-weight:300;
}
.please-con i{font-size:2rem}

.please .cover{
    position:fixed;
    top:0;
    right:0;
    width:100%;
    height:100%;
    background:rgba(0, 0, 0, 0.715);
}
.please .action{
    position:relative;
    top:3rem;
}
.please button small{
    text-transform: lowercase;
}
