.search{
    position:absolute;
    padding:0.5rem 1rem;
    box-sizing: border-box;
    height:100%;
    width:100%;
}
.search .wrapper{
    position:relative;
    top:1rem;
    height:0.5rem;
    left:0;
    right:0;
    border-right:1px solid white;
    border-left:1px solid white;
}
.search .search-input{
    position:relative;
    top:-1rem;
    width:100%;
    height:1.1rem;
    background:rgba(0, 0, 0, 0);
    border:0px solid black;
    border-bottom:1px solid white;
    padding:0.5rem 1.9rem 0.7rem 0.4rem;
    box-sizing: border-box;
    margin-bottom:-0.2rem;
    font-size:0.9rem;
    color:white;
    font-weight:100;
    letter-spacing:1px;
}
.search .search-input:focus{
    outline:none;
    background: rgba(0, 0, 0, 0.1)
}
.search .icon{
    position:absolute;
    top:-0.15rem;
    height:0.6rem;
    width:1.5rem;
    right:0rem;
    border-left:1px solid white;
    font-size:1rem;
    text-align:center;
}
.search .searchicon{
    position:relative;
    top:-0.9rem;
    color:white;
}