.mdl-layout__content.float {
  z-index: 4;
  overflow: hidden;
}
.download {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgb(0, 35, 59);
  z-index: 199;
  color: white;
  overflow-y: auto;
  overflow-x: hidden;
}

.download .con {
  position: absolute;
  left: 20%;
  width: 60%;
}
.download .info-text {
  font-size: 1.25rem;
  margin-top: 5rem;
}
/* .download div {border:1px solid red;} */
.download .id {
  position: absolute;
  top: 1.5rem;
  right: 0.5rem;
  padding: 2rem 0;
  width: 6rem;
  text-align: center;
  font-size: 2.5rem;
  background: white;
  color: #0c4147;
  font-weight: 500;
  z-index: 3;
  animation: attract 4000ms infinite;
}
@keyframes attract {
  0% {
    transform: rotate(-15deg) scale(0.8);
    box-shadow: 2px 2px 10px 2px #ffffff2c;
  }
  5% {
    transform: rotate(15deg) scale(1.3);
    box-shadow: 5px 5px 30px 3px #ffffff2c;
  }
  7.5% {
    transform: rotate(0deg) scale(1);
    box-shadow: 3px 3px 15px 2px #ffffff2c;
  }
  97.5% {
    box-shadow: 3px 3px 15px 2px #ffffff2c;
    transform: rotate(0deg) scale(1);
  }
  100% {
    transform: rotate(-15deg) scale(0.8);
    box-shadow: 2px 2px 10px 2px #ffffff2c;
  }
}
.download .worth {
  position: relative;
  width: 43.85rem;
  margin: auto;
  top: 2rem;
}
.download .worth div {
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  background: white;
  color: black;
}
.download .worth .info {
  border: 0px solid rgba(0, 0, 0, 0);
}
.download .worth .info * {
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.download .worth .carish {
  position: relative;
  width: 14rem;
  margin-right: 0.5rem;
  display: inline-block;
  height: 14rem;
  padding: 2rem;
  box-sizing: border-box;
}
.download .worth .carish .title {
  font-size: 1.4rem;
  display: block;
}
.download .worth .carish .val {
  position: relative;
  text-align: center;
  font-size: 5rem;
  display: block;
  padding: 2.7rem 0;
  color: rgb(0, 140, 255);
}
.download .worth .carish.size .val {
  color: rgb(1, 184, 98);
}
.download .worth .carish .disk {
  position: relative;
  text-align: center;
  display: block;
  font-size: 1.7rem;
}
.download .worth .info {
  position: absolute;
  top: 0;
  right: 0;
}

.download .list {
  position: relative;
  top: 4rem;
  width: 100%;
  max-width: 600px;
  margin: auto;
}

.download .list .head {
  position: relative;
  width: 100%;
  height: 3rem;
  font-size: 2rem;
}

.download .list .cart {
  position: relative;
  top: 0;
  right: 0;
  width: 100%;
}
.download .list button {
  position: absolute;
  right: 0;
  color: white;
}
.download .item {
  background: white;
  color: black;
  box-shadow: 0px 0px 0px 0px black;
}
@keyframes squish {
  from {
    margin-bottom: 10rem;
  }
  /* to{margin-bottom:1.3rem;} */
}
.download .item {
  animation: squish 300ms ease-in;
}
.download .item.red {
  background: rgb(252, 148, 148);
}
.download .start-download {
  margin-top: -1rem;
  color: white;
  background: rgb(0, 158, 61);
}
.download .modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 100;
}
.download .modal .con {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 35%;
  padding: 2rem 1rem;
  background: white;
  border-radius: 5px;
  color: black;
  max-width: 20rem;
  min-height: 8rem;
  text-align: center;
}
.download .modal .con p {
  font-size: 1.2rem;
}
.download .modal .con ul {
  text-align: left;
}
.download .modal .con li {
  font-size: 1.3rem;
}
.download .modal .con .start-download i {
  font-size: 3.5rem;
}
.download .modal .con button.start-download {
  position: absolute;
  right: 1rem;
  bottom: 1rem;
}

@media screen and (max-width: 1070px) {
  .download .con {
    left: 10%;
    width: 80%;
  }
}
@media screen and (max-width: 841px) {
  .download .cart {
    position: relative;
    top: 0;
    left: 0;
    height: unset;
    background: unset;
    overflow: unset;
    padding: 0;
    bottom: unset;
  }
  .download .cart-list {
    top: 0;
  }
}
@media screen and (max-width: 790px) {
  .download .con {
    left: 1%;
    width: 98%;
  }
  .download .modal .con {
    left: 30%;
  }
}

@media screen and (max-width: 660px) {
  .download .break {
    position: relative;
    width: 29rem;
    margin: auto;
    display: block;
    margin-bottom: 1rem;
  }
  .download .worth .info {
    position: relative;
    top: 0;
    left: 0;
    width: 14.5rem;
    margin: auto;
  }
  .download .worth {
    width: 100%;
  }
}
@media screen and (max-width: 660px) {
  .download .break {
    position: relative;
    width: 29rem;
    margin: auto;
    display: block;
    margin-bottom: 1rem;
  }
}
@media screen and (max-width: 570px) {
  .download .modal .con {
    left: 25%;
  }
}
@media screen and (max-width: 470px) {
  .download .modal .con {
    left: 15%;
  }
}
@media screen and (max-width: 430px) {
  .download .break {
    width: 14rem;
  }
  .download .carish {
    margin-bottom: 1rem;
  }
}
@media screen and (max-width: 400px) {
  .download .modal .con {
    left: 1rem;
    /* right:5rem; */
    min-width: 85%;
    max-width: 80%;
  }
}
@media screen and (max-width: 285px) {
  .download .info-text {
    position: relative;
    top: 4rem;
    margin-bottom: 4rem;
    padding: 0 0.3rem;
  }
  .download .id {
    top: 8.5rem;
    right: unset;
    left: 35%;
    transform: translateX(-50%);
  }
}
@media screen and (max-width: 240px) {
  .download {
    overflow: scroll;
  }
}
.download .go-back-btn {
  position: absolute;
  top: 0.5rem;
  left: 0.5rem;
  width: 9rem;
  color: black;
  /* line-height: 0; */
}
