body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.absolute {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
img {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
}
.corner-left {
  position: absolute;
  top: 0;
  left: 0;
}
.corner-right {
  position: absolute;
  top: 0;
  right: 0;
}
.corner-both {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}
.corner-both-bottom {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}
.corner-right-bottom {
  position: absolute;
  right: 0;
  bottom: 0;
}
.corner-left-bottom {
  position: absolute;
  left: 0;
  bottom: 0;
}
@media screen and (min-width: 768px) {
  ::-webkit-scrollbar {
    background: rgba(110, 110, 110, 0.147);
    width: 0.45rem;
  }
  ::-webkit-scrollbar-thumb {
    background: rgb(119, 119, 119);
    border-radius: 5px;
  }
  ::-moz-scrollbar {
    background: rgba(110, 110, 110, 0.147);
    width: 0.6rem;
  }
  ::-moz-scrollbar-thumb {
    background: rgb(119, 119, 119);
    border-radius: 5px;
  }
}
.mdl-button[disabled][disabled],
.mdl-button.mdl-button--disabled.mdl-button--disabled {
  color: rgba(100, 100, 100, 0.85);
  cursor: default;
  background-color: transparent;
}
.mdl-button--raised[disabled][disabled],
.mdl-button--raised.mdl-button--disabled.mdl-button--disabled {
  background-color: rgba(100, 100, 100, 0.22);
  color: rgba(150, 150, 150, 1);
  box-shadow: none;
}

body .mdl-layout__tab-bar {
  overflow-x: hidden !important;
}

.store-body {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.stores {
  position: relative;
  top: 5rem;
  width: 70%;
  margin: auto;
}
@media screen and (max-width: 400px) {
  .stores {
    width: 90%;
  }
}
@media screen and (max-width: 550px) {
  .stores {
    width: 85%;
  }
}
@media screen and (min-width: 700px) {
  .stores {
    width: 50%;
  }
}
.stores .store {
  position: relative;
  height: 5rem;
  width: 100%;
  padding: 0.5rem;
  margin-bottom: 1.5rem;
  border-radius: 5px;
  background: white;
  border: 1px solid #ddd;
  box-shadow: 2px 2px 5px 1px rgba(221, 221, 221, 0.485);
  cursor: pointer;
}
.stores .store:hover {
  background: #ddd;
}
.stores .store .img {
  position: relative;
  top: 0;
  left: 0;
  width: 5rem;
  height: 100%;
}
.stores .store .img img {
  position: relative;
  top: 0.5rem;
  height: 4rem;
  width: 4rem;
}
.stores .store .text {
  position: absolute;
  left: 5rem;
  right: 0;
  top: 0;
  bottom: 0;
  padding: 1.1rem 0;
}
.stores .store .text .name {
  font-weight: bold;
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
  text-transform: capitalize;
}
.stores .store .text .address {
  /* font-size:0.9rem; */
  /* font-weight:lighter; */
}
.stores .store .text .distance {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  height: 100%;
  /* background:red; */
}
.stores .store .text .distance .loc {
}

.stores .store .store-status {
  position: absolute;
  bottom: 1rem;
  right: 0.5rem;
  font-size: 1.1rem;
}
.stores .store .store-status .status-con {
  font-weight: bold;
}
.stores .store .store-status .status-con.active {
  color: rgb(0, 143, 88);
}
.stores .store .store-status .status-con.closed {
  color: rgb(212, 0, 64);
  font-weight: normal;
}


.stores .store .store-status .status-con i {
  display: inline;
  font-size: 1.1rem;
  position: relative;
  top: 0.15rem;
}
