.notify{
    position: relative;
    top:0;
    left:0;
    padding:2rem 0;
}
.notify .item{
    position: relative;
    width:60%;
    margin:auto;
    border-radius:10px;
    overflow: hidden;
    margin-bottom:1rem;
    border:1px solid #ddd;
    box-shadow:2px 2px 3px 1px rgba(221, 221, 221, 0.405);
}
.notify .hr{
    position: relative;
    width:60%;
    text-align:center;
    font-size:1.25rem;
    margin:4rem auto 1rem auto;
    border-top:2px solid #aaa;
    padding-top:0.5rem;
}
@media screen and (max-width:1000px){
    .notify .item,.notify .hr{
        width:80%;
    }
}
@media screen and (max-width:500px){
    .notify .item,.notify .hr{
        width:90%;
    }
}
@media screen and (max-width:350px){
    .notify .item,.notify .hr{
        width:95%;
    }
}
.notify .item .head{
    position: relative;
    top:0;
    left:0;
    height:2.5rem;
    padding:0.5rem 1rem;
    border-bottom:1px solid rgba(0,0,0,0.1);
    background: rgba(0,0,0,0.025);
}
.notify .item .img{
    width:2.5rem;
    height: 2.5rem;
    border-radius:50%;
    overflow: hidden;
}
.notify .item .img img{
    width:100%;
    height:100%; 
}
.notify .item .head .text{
    position:absolute;
    top:0.5rem;
    left:4.5rem;
}
.notify .item .head .text .name{
    font-size:1.25rem;
    font-weight:500;
    text-transform: capitalize;
}
.notify .item .head .text .time{
    color:#aaa;
}
.notify .item .body{
    padding:1.5rem 1rem 2.5rem 1rem;
    font-size:1.3rem;
    word-wrap: break-word;
}
.notify .empty{
    position: relative;
    top:7.5rem;
    width:19rem;
    text-align: center;
    font-size:1.7rem;
    color:rgba(150,150,150,0.75);
    margin:auto;
}
.notify .empty i{
    display:block;
    font-size:6rem;
    margin-bottom:0.5rem;
}
.notify .additional{
    text-align:center;
    font-size:1.2rem;
    font-weight: bold;
    margin-top:-1rem;
    margin-bottom:0.5rem;
}
@media screen and (max-width:350px){
    .notify .item .head{
        padding:0.5rem;
    }
    .notify .item .body{
        padding:1rem 0.5rem 2rem 0.5rem;
    }
    .notify .item .head .text{
        top:0.5rem;
        left:4.5rem;
    }
.notify .empty{
    width:15rem;
    font-size:1.3rem;
}
.notify .empty i{
    font-size:4rem;
}
}
